import * as  config from './../common-var/config';
import { IViewAbility } from './IViewAbility';
declare var window: any;
declare var $sf: any;
export class SafeFrame implements IViewAbility {
    isSupported = (cb?: Function): boolean => {
        let status = true;
        if (typeof this.extern === 'undefined') {
            status = false;
        }
        if (typeof this.ad === 'undefined') {
            status = false;
        }
        if (typeof cb === 'function') {
            cb(status)
        }
        return status;
    }
    extern: any;
    ad: HTMLElement;
    isOk = false;
    inView = false;
    constructor(el: HTMLElement) {
        this.ad = el;
        if (typeof $sf !== 'undefined' || typeof window.extern !== 'undefined')
            this.extern = $sf.ext;
        if (this.isSupported())
            this.register();
    }

    /**
* This is the event listener to capture events from the publisher host page.
*/
    statusUpdate = (status: string, data: string) => {
        //debugger;
        if (status == 'expanded') {
        }
        else if (status == 'geom-update') {
            this.updateInView();
        }
    }
    isView = () => {
        return this.inView;
    }

    updateInView = () => {
        console.log('Percentage:' + this.extern.inViewPercentage())
        let ratio = this.ad.scrollWidth * this.ad.scrollHeight >= config.largeSize ? config.largeReduce : config.defaultReduce;
        this.inView = this.extern.inViewPercentage() >= ratio * 100 ? true : false;
        console.log(this.isView());
    }
    register = () => {
        if (this.extern) {
            try {
                this.extern.register(this.ad.scrollWidth, this.ad.scrollHeight, this.statusUpdate);
            } catch (e) {
                console.log('Exception or no safeframes available: ' + e.message);
            }
        }
    }
}


