import { IViewAbility } from './IViewAbility';
declare var mraid: any;

export class MraidAPI implements IViewAbility {
    isReady = false;
    constructor() {
        if (typeof mraid !== 'undefined') {
            this.doReadyCheck();
        }
    }
    isView = (): boolean => {
        return mraid.isViewable();
    }
    doReadyCheck = () => {
        if (mraid.getState() == 'loading') {
            mraid.addEventListener('ready', this.mraidIsReady);
        } else {
            this.isReady = true
        }
    }
    mraidIsReady = () => {
        mraid.removeEventListener('ready', this.mraidIsReady);
    }
    isSupported = (cb?: Function): boolean => {
        let status = true;
        if (this.isReady) {
            status = false;
        }
        if (typeof cb === 'function') {
            cb(status)
        }
        return status;
    }
}


