import { RequestObj } from './RequestObj';
import * as  config from './../common-var/moq.config';
export class Http {
    xhr: any
    constructor() {
        this.xhr = this.createXHR();
    }
    private createXHR = () => {
        if (typeof XMLHttpRequest != 'undefined') {
            return new XMLHttpRequest();
        } else if (typeof ActiveXObject != 'undefined') {
            var version = [
                'MSXML2.XMLHttp.6.0',
                'MSXML2.XMLHttp.3.0',
                'MSXML2.XMLHttp'
            ];
            for (var i = 0; version.length; i++) {
                try {
                    return new ActiveXObject(version[i]);
                } catch (e) {
                    console.log(e)
                }
            }
        } else {
            throw new Error('system do not support XHR object');
        }
    }
    private params = (data: any) => {
        var arr = [];
        for (var i in data) {
            arr.push(encodeURIComponent(i) + '=' + encodeURIComponent(data[i]));
        }
        return arr.join('&');
    }
    apiCall = (obj: RequestObj) => {
        var xhr = this.createXHR();
        if (obj.method === 'get') {
            obj.data = this.params(obj.data);
            obj.url += obj.url.indexOf('?') == -1 ? '?' + obj.data : '&' + obj.data;
        }
        if (obj.async === true) {
            xhr.onreadystatechange = function () {
                if (xhr.readyState == 4) {
                    callback();
                }
            };
        }
        if(config.dev_stage)
        {
          //  console.log(obj);
            return;
        }
        xhr.open(obj.method, obj.url, obj.async);
        if (obj.method === 'post') {
            xhr.setRequestHeader('Content-Type', 'application/json;charset=UTF-8');
            xhr.send(JSON.stringify(obj.data));
        } else {
            xhr.send(null);
        }
        if (obj.async === false) {
            callback();
        }

        function callback() {
            if (xhr.status == 200) {
                obj.success(xhr.responseText);
            } else {
                console.log('bad request' + xhr.status + 'error msg' + xhr.statusText);
            }
        }
    }
}



