import { SmartView } from './viewability/SmartView';
import { Params } from './var/Params';
import { IframeClickTracker } from './event-tracker/IframeClickTracker';
import { AdInformation } from './var/AdInformation';
import { Map } from './var/Map.interface';
import { IViewAbility } from './viewability/IViewAbility';
import { Http } from './http/Http';
import { RequestObj } from './http/RequestObj';
declare var document: any;
declare var window: any;
declare var mraid: any;
export class Moq {

  trackStart = false;
  viewDuration = 0;
  updateDuration = 0;
  hidden = 'hidden';
  isHidden: boolean;
  lastHiddenStauts: boolean;
  srcParm: Map;
  tracker: any;
  adTrackInterval: any;
  params: Params;
  adInformation: AdInformation;
  ad: HTMLElement;
  count = 1;
  activeIntersection = false;
  intersectionView = false;
  testMethod = 0;
  methodName: string;
  viewAbility: IViewAbility;
  myScript: HTMLScriptElement;
  clicked = 0;
  constructor() {

    if (this.hidden in document)
      document.addEventListener('visibilitychange', this.onchange);
    else if ((this.hidden = 'mozHidden') in document)
      document.addEventListener('mozvisibilitychange', this.onchange);
    else if ((this.hidden = 'webkitHidden') in document)
      document.addEventListener('webkitvisibilitychange', this.onchange);
    else if ((this.hidden = 'msHidden') in document)
      document.addEventListener('msvisibilitychange', this.onchange);
    // IE 9 and lower:
    else if ('onfocusin' in document)
      document.onfocusin = document.onfocusout = this.onchange;
    // All others:
    else
      window.onpageshow = window.onpagehide
        = window.onfocus = window.onblur = this.onchange;

    if (document.hidden !== undefined)
      this.onchange({ type: document.hidden ? 'blur' : 'focus' });
    let scripts = document.getElementsByTagName('script');
    var re = /moq.js/gi;
    for (var i = 0; i < scripts.length; i++) {
      if (scripts[i].outerHTML.search(re) != -1) {
        this.myScript = scripts[i];
        break;
      }
    }

    let queryString = this.myScript.src.replace(/^[^\?]+\??/, '');
    this.srcParm = this.parseQuery(queryString);

    try {
      let args = new Params();
      args.impid = this.srcParm['impid'].prop;
      args.ep = this.srcParm['ep'].prop;
      args.reqh = parseInt(this.srcParm['reqh'].prop);
      args.reqw = parseInt(this.srcParm['reqw'].prop);
      this.params = args;
      this.testMethod = parseInt(this.srcParm['test'].prop);
    } catch (error) {
      console.log(error);
    }
    this.adInformation = new AdInformation();
    this.getAdElement();
  }
  private getAdElement = () => {
    //console.log('getAdElement');
    if (!this.ad) {
      this.searchNodes(this.myScript.parentNode)
    }
  }
  private searchNodes = (node: any) => {
    var self = this;
    if (node.nodeType == 1) {
      if (node.hasChildNodes) {
        var sonnodes = node.childNodes;
        for (var i = 0; i < sonnodes.length; i++) {
          var sonnode = sonnodes.item(i);
          if (sonnode.nodeName !== 'SCRIPT') {
            if (sonnode.scrollWidth && sonnode.scrollHeight) {
              if (this.btw5(sonnode.scrollWidth, this.params.reqw) && this.btw5(sonnode.scrollHeight, this.params.reqh)) {
                this.ad = sonnode;
                if (this.ad.tagName === 'IFRAME') {
                  new IframeClickTracker().track(this.ad, function () {
                    window.clicked = self.clicked += 1;
                    self.updateTrackObj(ApiAction.clicked)
                  })
                } else {
                  this.ad.addEventListener('click', function () {
                    window.clicked = self.clicked += 1;
                    self.updateTrackObj(ApiAction.clicked)
                  });
                }
                this.ad.style.msTouchAction = 'manipulation';
                this.ad.style.touchAction = 'manipulation';
                // alert(this.ad);
                break;
              }
            }
            this.searchNodes(sonnode);
          }
        }
      }
    }
  }

  private btw5 = (val: number, limit: number) => {
    return val >= limit - 10 && val <= limit + 10;
  }
  private onchange = (evt: any) => {
    var v = 'visible', h = 'hidden';
    var evtMap: any;
    evtMap = {
      focus: v, focusin: v, pageshow: v, blur: h, focusout: h, pagehide: h
    };
    evt = evt || window.event;
    if (evt.type in evtMap)
      this.isHidden = evtMap[evt.type] == h;
    else
      this.isHidden = document.hidden;
    //console.log('is hidden :' + this.isHidden);
  }
  private parseQuery = (query: string) => {
    var Params: Map = {};
    if (!query) return Params; // return empty object
    let Pairs = query.split(/[;&]/);
    for (let i = 0; i < Pairs.length; i++) {
      let KeyVal = Pairs[i].split('=');
      if (!KeyVal || KeyVal.length != 2) continue;
      let key = KeyVal[0];
      let val = KeyVal[1];
      val = val.replace(/\+/g, ' ');
      Params[key] = { prop: val };
    }
    return Params;
  }
  private GetTrackObj = (): AdInformation => {

    return this.adInformation;
  }
  updateTrackObj = (action: string) => {
    //create a request object
    var requestObj = new RequestObj(action, this.params.impid);
    // set a update data
    requestObj.data = this.adInformation;
    //call remote api
    var http = new Http().apiCall(requestObj);
  }
  startTracking = () => {
    this.tracker = setInterval(this.tracking, 100)
    setTimeout(this.stopTracker, 1000 * 300);
  }

  private tracking = () => {
    let isView = false;
    //update initial infor 
    if (!this.trackStart) {
      this.trackStart = true;
      this.updateTrackObj(ApiAction.init);
    }
    // if (typeof this.ad === 'undefined' || this.ad === null) {
    if (!this.ad) {
      this.getAdElement();
    }
    if (!this.ad) {
      // alert('no ad')
      return
    }
    // if (typeof this.viewAbility === 'undefined') {
    if (!this.viewAbility) {
      let m = this.testMethod;
      let viewAbility = new SmartView(this.ad)
      this.viewAbility = viewAbility.getMethod(m);
      this.methodName = viewAbility.methodName;
      //console.log(this.methodName);
    }
    if (!this.viewAbility) {
        // alert('no method')
      return
    }
    isView = typeof this.viewAbility === 'undefined' ? false : (this.viewAbility.isView() && !this.isHidden);
    window.isAdView = isView;
    window.isActive = !this.isHidden;
    // alert('view:'+window.isAdView+'  Active:'+window.isActive)
    //console.log('isAdView:' + window.isAdView + 'isActive:' + window.isActive)
    //update tos and view duration
    if (this.adInformation.timeOnSite < 120 * 1000) {
      // if in view add tos and view time 
      if (isView) {
        this.adInformation.timeOnSite += 100
        this.viewDuration += 100
        //set longest v
        if (this.adInformation.viewDuration < this.viewDuration) {
          this.adInformation.viewDuration = this.viewDuration
        }
        if (this.adInformation.timeOnSite < 1000) {
          this.updates()
        } else if (this.adInformation.timeOnSite < 10 * 1000) {
          if (this.checkUpdateTime(this.adInformation.timeOnSite, 1000)) {
            this.updates()
          }
        } else {
          if (this.checkUpdateTime(this.adInformation.timeOnSite, 5 * 1000)) {
            this.updates()
          }
        }
      } else {
        this.viewDuration = 0;
      }
    }
  }
  private checkUpdateTime = (tos: number, seed: number): boolean => {
    return tos % seed ? false : true;
  }
  private updates = () => {
    let sec = Math.floor(this.adInformation.viewDuration / 1000);
    if (this.updateDuration < sec) {

      this.updateDuration = sec;
      this.updateView();
    }
    this.updateTOS();
  }
  private updateTOS = () => {

    //create a request object
    var requestObj = new RequestObj(ApiAction.tos, this.params.impid);
    // set a update data
    requestObj.data = { timeOnSite: this.adInformation.timeOnSite };
    //call remote api
    var http = new Http().apiCall(requestObj);

  }
  private updateView = () => {
    //create a request object
    var requestObj = new RequestObj(ApiAction.view, this.params.impid);
    // set a update data
    requestObj.data = { viewDuration: this.adInformation.viewDuration };
    //call remote api
    var http = new Http().apiCall(requestObj);
  }
  private updateClick = () => {

    //create a request object
    var requestObj = new RequestObj(ApiAction.view, this.params.impid);
    // set a update data
    requestObj.data = this.adInformation;
    //call remote api
    var http = new Http().apiCall(requestObj);

  }

  stopTracker = () => {
    clearInterval(this.tracker);
  }

  test = () => {
    alert('work!');
  }

  iframeClickTest = () => {
    let info = document.createElement('h1');
    info.innerText = '0';
    document.body.appendChild(info);
    new IframeClickTracker().track(this.ad, function () {
      info.innerText = (parseInt(info.innerText) + 1).toString();
    })
  }
}
  new Moq().startTracking();
class ApiAction {
  static clicked = 'clicked';
  static tos = 'tos';
  static init = 'init';
  static view = 'view';
}


