export class IframeClickTracker {
    private resolution = 200;
    iframes: Iframe[]=[];
    interval: any = null;

    track=(element: HTMLElement, cb: Function) =>{
        this.iframes.push(new Iframe(element, cb));
        if (!this.interval) {
            let self =this;
            this.interval = setInterval(function () {self.checkClick(); }, this.resolution);
        }
    }
    checkClick=()=> {
        if (document.activeElement) {
            var activeElement = document.activeElement;
            for (var i in this.iframes) {
                if (activeElement === this.iframes[i].element) {
                    if (this.iframes[i].hasTracked === false) {
                        this.iframes[i].cb.apply(window, []);
                        this.iframes[i].hasTracked = true;
                    }
                } else {
                    this.iframes[i].hasTracked = false;
                }
            }
        }
    }
}
class Iframe {
    element: HTMLElement;
    cb: Function;
    hasTracked = false;
    constructor(el: HTMLElement, func: Function) {
        this.element = el;
        this.cb = func;
    }
}



