import { IViewAbility } from './IViewAbility';

declare var window: any;
declare var performance: any;
window.performance = window.performance || {};
performance.now = (function () {
    return performance.now ||
        performance.mozNow ||
        performance.msNow ||
        performance.oNow ||
        performance.webkitNow ||
        function () {
            return new Date().getTime();
        };
})();

export class RequestAnimationFrame implements IViewAbility {
    isSupported(cb?: Function): boolean {
        if (typeof requestAnimationFrame == 'undefined' || typeof this.ad == 'undefined' || this.ad == null || !this.passTest) {
            return false;
        }
        return true;

    }
    passTest: boolean
    delta = 250;
    viewable = false;
    frame: HTMLElement;
    last = 0;
    ad: any
    constructor(el: any) {
        let frame = document.createElement('iframe');
        el.parentNode.appendChild(frame);
        frame.id = 'VisFrame1';
        frame.style.width = '4px';
        frame.style.height = '4px';
        frame.style.backgroundColor = 'blue';
        frame.style.verticalAlign = 'top';
        frame.scrolling = 'no';
        frame.frameBorder = '0';
        
        var top = el.offsetTop;
        var left = el.offsetLeft;
        var width = el.offsetWidth;
        var height = el.offsetHeight;

        while (el.offsetParent) {
            el = el.offsetParent;
            top += el.offsetTop;
            left += el.offsetLeft;
        }
        frame.style.marginTop = '-'+Math.round((height+top) / 2) + 'px';
        frame.style.marginLeft = Math.round((width+left) / 2) + 'px';

        this.listen();
        this.frame = frame;
        frame.src = 'data:text/html;charset=UTF-8,' + '<html><head></head><body><script>window.parent.postMessage("yo","*"); function call(){window.parent.postMessage("hi","*"),window.requestAnimationFrame(call)}call();</script></body></html>';
        //this.test();
    }
    test = () => {
        let isPass = false;
        var self = this;
        var time = self.time();
        if (!self.isWiredOk()) {
            this.passTest = false
        }
        self.move(window.screen.width + 5000, window.screen.height + 5000);
        cm.doYieldMsec(function () {
            if (self.isView()) {
                this.passTest = false;
            }
            this.passTest = true;
            self.move(-5000, -5000);
        }, 100);
    }
    time = () => {
        return performance.now();
    }

    listen = () => {
        var self = this;
        self.last = 0; /* last time we received an update */

        window.addEventListener('message', function (e: any) {
            var time = self.time();
            var diff = (time - self.last);
            self.last = time;
            var old = self.delta;
            self.delta = Math.round((self.delta + diff) / 2);

        });
    }
    isWiredOk = () => {
        return (this.last > 0);
    }
    isView = () => {
        if (this.isSupported())
            return false;
        var avg = (((this.time() - this.last) + this.delta) / 2);
        return (avg < 55);
    }
    move = (x: any, y: any) => {
        this.frame.style.top = y + 'px';
        this.frame.style.left = x + 'px';
    }
}
class cm {
    static doYieldMsec(fn: any, msec: number) {
        window.setTimeout(function () {
            window.setTimeout(fn, 10);
        }, msec);
    }

    static log(msg: string) {
        var e = document.createElement('p');
        e.innerHTML = Math.round(performance.now()) + '<span style=\'padding-left: 15px; color: #666\'>' + msg + '</span>';
        document.getElementById('msgs').appendChild(e);
    }

    static testResult(passed: boolean, msg = '') {
        if (msg) {
            this.log(msg);
            document.getElementById('testMsg').innerText = msg;
        }

        var el = document.getElementById('testResult');
        if (passed) {
            el.innerText = 'PASSED';
            el.style.color = 'green';
        } else {
            el.innerText = 'FAILED';
            el.style.color = 'red';
            document.getElementById('testMsg').innerText = msg;
        }
    }
}




