import * as  config from './../common-var/moq.config';
export class RequestObj {
    constructor(action:string,impid:string){
        this.url=config.remote_api+'/'+action+'/'+impid;
    }
    url:string;
    method = 'post';
    data?: any;
    async= true;
    responseData?: string;
    success = (res: string) => {
        this.responseData = res;
    }
}



