import { ScreenDimensions } from './ScreenDimensions'
import { AdSize } from './AdSize'
import { ViewMethod } from './ViewMethod'
import { Params } from './Params'
import { Navigator } from './Navigator'
export class AdInformation {

    constructor() {
        let nav=new Navigator();
        this.userAgent =nav.GetUserAgent();
        this.pixelDepth =nav.GetpixelDepth();
        this.connectionType =nav.GetConnectionType();
        this.platform =nav.GetPlatform();
        this.touchPoints =nav.GetTouchPoints();
        this.xDomain=window.location != window.parent.location
    }
    userAgent: string;
    screenDimensions: ScreenDimensions;
    adSize: AdSize;
    pixelDepth: number;
    connectionType: string;
    platform: string;
    touchPoints: number;
    realDomain: string;
    xDomain: boolean;
    timeOnSite: number=0;
    viewDuration: number=0;
    params: Params;
}


