import * as  config from './../common-var/config';
import { IViewAbility } from './IViewAbility';
export class IntersectionObserverAPI implements IViewAbility {
    isSupported(cb?:Function): boolean {
         let status=true;
        if (typeof this.ad === 'undefined' || this.ad == null  || typeof IntersectionObserver === 'undefined') {
            status= false;
        }
         if (typeof cb === 'function') {
            cb(status)
        }
        return status;
    }
    private inView: boolean
    ad: HTMLElement
    constructor(el: HTMLElement) {
        this.ad=el;
      let ratio=  this.ad.scrollWidth*this.ad.scrollHeight>=config.largeSize?config.largeReduce:config.defaultReduce;
      console.log('duration:'+ratio)
        let observer = new IntersectionObserver((enters) => {
            if (enters[0].intersectionRatio < ratio) {
                this.inView = false;
            } else {
                this.inView = true;
            }
        }, {
                threshold: [ratio]
            })

        observer.observe(this.ad)
    }

    isView = () => {
        return this.inView;
    }
}


