import { MraidAPI } from './MraidAPI';
import { SafeFrame } from './SafeFrame';
import { RequestAnimationFrame } from './RequestAnimationFrame';
import { IntersectionObserverAPI } from './IntersectionObserverAPI';
import { Geometry } from './Geometry';
import { IViewAbility } from './IViewAbility';
export class SmartView {

  ad: HTMLElement;
  viewAbility: IViewAbility;
  methodName='';
  constructor(el: HTMLElement) {
    this.ad = el;
  }

  getMethod = (m: number): IViewAbility => {

    switch (m) {
      case 0:
        var geometry = new Geometry(this.ad);
        if (geometry.isSupported()) {
          this.methodName = 'geometry'
          return geometry;
        }

        var intersectionObserverAPI = new IntersectionObserverAPI(this.ad);
        if (intersectionObserverAPI.isSupported()) {
          this.methodName = 'intersectionObserver'
          return intersectionObserverAPI;
        }

        var safeFrame = new SafeFrame(this.ad)
        if (safeFrame.isSupported()) {
          this.methodName = 'safeFrame'
          return safeFrame;
        }

        var requestAnimationFrame = new RequestAnimationFrame(this.ad);
        if (requestAnimationFrame.isSupported()) {
          this.methodName = 'requestAnimationFrame'
          return requestAnimationFrame;
        }

        var mraidAPI = new MraidAPI();
        if (mraidAPI.isSupported()) {
          this.methodName = 'mraid'
          return mraidAPI;
        }

        break;
      case 1:
        this.methodName = 'geometry'
        return new Geometry(this.ad);
      case 2:
        this.methodName = 'intersectionObserver'
        return new IntersectionObserverAPI(this.ad);
      case 3:
        this.methodName = 'safeFrame'
        return new SafeFrame(this.ad);
      case 4:
        this.methodName = 'requestAnimationFrame'
        return new RequestAnimationFrame(this.ad);
      case 5:
        this.methodName = 'mraid'
        return new MraidAPI();
      default:
        return null;
    }
    return null;
  }
}




